import { supabase } from '../lib/supabase';

export interface AIResponse {
  success: boolean;
  message: string;
  suggestions?: string[];
  data?: any;
}

export class AIService {
  // AI destekli giri yardm
  static async getLoginAssistance(email: string, errorType?: string): Promise<AIResponse> {
    try {
      // Kullancnn giri gemiini kontrol et
      const { data: loginHistory } = await supabase
        .from('spendme_login_attempts')
        .select('*')
        .eq('email', email)
        .order('created_at', { ascending: false })
        .limit(5);

      // Hata tipine gre neriler
      const suggestions: string[] = [];
      
      if (errorType === 'invalid_credentials') {
        suggestions.push('ifrenizi kontrol edin ve byk/kk harflere dikkat edin');
        suggestions.push('Caps Lock tuunun ak olmadndan emin olun');
        suggestions.push('ifrenizi unuttuysanz "ifremi Unuttum" seeneini kullann');
      } else if (errorType === 'user_not_found') {
        suggestions.push('Bu e-posta adresi ile kaytl hesap bulunamad');
        suggestions.push('Yeni hesap oluturmak iin "Kayt Ol" seeneini kullann');
        suggestions.push('E-posta adresinizi kontrol edin');
      } else if (errorType === 'too_many_requests') {
        suggestions.push('ok fazla baarsz giri denemesi yapld');
        suggestions.push('Birka dakika bekleyip tekrar deneyin');
        suggestions.push('ifrenizi sfrlamay dnn');
      }

      // Akll neriler
      if (loginHistory && loginHistory.length > 0) {
        const lastLogin = loginHistory[0];
        const timeDiff = Date.now() - new Date(lastLogin.created_at).getTime();
        const hoursDiff = timeDiff / (1000 * 60 * 60);
        
        if (hoursDiff > 24) {
          suggestions.push('Son giriinizden bu yana 24 saat geti. Hesabnzn gvenlii iin ifrenizi gncellemeyi dnn');
        }
      }

      return {
        success: true,
        message: 'AI destekli giri yardm hazrland',
        suggestions
      };
    } catch (error) {
      return {
        success: false,
        message: 'AI yardm alnrken hata olutu',
        suggestions: ['Tekrar deneyin', 'Manuel olarak giri yapmay deneyin']
      };
    }
  }

  // Akll hesap oluturma nerileri
  static async getSignupSuggestions(email: string): Promise<AIResponse> {
    try {
      // E-posta domain'ine gre neriler
      const domain = email.split('@')[1];
      const suggestions: string[] = [];
      
      if (domain === 'gmail.com') {
        suggestions.push('Gmail hesabnzla hzl giri yapabilirsiniz');
        suggestions.push('Gl bir ifre sein (en az 8 karakter)');
      } else if (domain === 'outlook.com' || domain === 'hotmail.com') {
        suggestions.push('Outlook hesabnzla hzl giri yapabilirsiniz');
        suggestions.push('ki faktrl dorulama kullanmanz neririz');
      } else {
        suggestions.push(' e-postanzla gvenli giri yapabilirsiniz');
        suggestions.push('irket gvenlik politikalarnza uygun ifre sein');
      }

      // Genel gvenlik nerileri
      suggestions.push('ifrenizde byk/kk harf, rakam ve zel karakter kullann');
      suggestions.push('Kiisel bilgilerinizi ifre olarak kullanmayn');
      suggestions.push('Hesabnz oluturduktan sonra profil bilgilerinizi tamamlayn');

      return {
        success: true,
        message: 'Akll hesap oluturma nerileri hazrland',
        suggestions
      };
    } catch (error) {
      return {
        success: false,
        message: 'AI nerileri alnrken hata olutu',
        suggestions: ['Gl bir ifre sein', 'E-posta adresinizi doru girdiinizden emin olun']
      };
    }
  }

  // Giri denemesi kaydetme
  static async logLoginAttempt(email: string, success: boolean, errorType?: string): Promise<void> {
    try {
      await supabase
        .from('spendme_login_attempts')
        .insert({
          email,
          success,
          error_type: errorType,
          ip_address: 'client_ip', // Gerek uygulamada IP adresi alnr
          user_agent: navigator.userAgent
        });
    } catch (error) {
      console.error('Login attempt logging failed:', error);
    }
  }

  // Akll ifre gllk kontrol
  static checkPasswordStrength(password: string): {
    score: number;
    feedback: string[];
    isStrong: boolean;
  } {
    const feedback: string[] = [];
    let score = 0;

    // Uzunluk kontrol
    if (password.length >= 8) {
      score += 1;
    } else {
      feedback.push('ifre en az 8 karakter olmaldr');
    }

    // Byk harf kontrol
    if (/[A-Z]/.test(password)) {
      score += 1;
    } else {
      feedback.push('En az bir byk harf kullann');
    }

    // Kk harf kontrol
    if (/[a-z]/.test(password)) {
      score += 1;
    } else {
      feedback.push('En az bir kk harf kullann');
    }

    // Rakam kontrol
    if (/\d/.test(password)) {
      score += 1;
    } else {
      feedback.push('En az bir rakam kullann');
    }

    // zel karakter kontrol
    if (/[!@#$%^&*(),.?":{}|<>]/.test(password)) {
      score += 1;
    } else {
      feedback.push('En az bir zel karakter kullann');
    }

    // Yaygn ifre kontrol
    const commonPasswords = ['password', '123456', 'qwerty', 'admin', 'letmein'];
    if (commonPasswords.includes(password.toLowerCase())) {
      score -= 2;
      feedback.push('Yaygn ifreler kullanmayn');
    }

    return {
      score,
      feedback,
      isStrong: score >= 4
    };
  }

  // E-posta format kontrol
  static validateEmail(email: string): {
    isValid: boolean;
    suggestions: string[];
  } {
    const suggestions: string[] = [];
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const isValid = emailRegex.test(email);

    if (!isValid) {
      suggestions.push('Geerli bir e-posta adresi girin');
      suggestions.push('rnek: kullanici@domain.com');
    }

    // Domain kontrol
    const domain = email.split('@')[1];
    if (domain && domain.length < 3) {
      suggestions.push('E-posta domain\'i ok ksa grnyor');
    }

    return {
      isValid,
      suggestions
    };
  }
} 