  // Markdown formatn temizleyen yardmc fonksiyon
  private static cleanGeminiResponse(response: string): string {
    if (response.includes('```json')) {
      return response.replace(/```json\n?/g, '').replace(/```\n?/g, '').trim();
    }
    return response;
  }

  // Akll ilem kategorilendirme
  static async categorizeTransaction(description: string, amount: number, type: 'income' | 'expense'): Promise<{
    category: string;
    confidence: number;
    suggestions: string[];
  }> {
    try {
      const prompt = `
        Aadaki ilem aklamasn analiz ederek en uygun kategoriyi ner:
        
        lem: ${description}
        Tutar: ${amount} TL
        Tr: ${type === 'income' ? 'Gelir' : 'Gider'}
        
        Trke kategoriler:
        Gelir: Maa, Ek Gelir, Yatrm, Kira Geliri, Dier
        Gider: Market, Ulam, Faturalar, Salk, Elence, Eitim, Giyim, Ev, Dier
        
        Sadece JSON formatnda yantla:
        {
          "category": "kategori_ad",
          "confidence": 0.95,
          "suggestions": ["neri1", "neri2", "neri3"]
        }
      `;

      const response = await this.callGeminiAPI(prompt);
      return JSON.parse(this.cleanGeminiResponse(response));
    } catch (error) {
      console.error('Gemini categorization error:', error);
      return {
        category: 'Dier',
        confidence: 0.5,
        suggestions: []
      };
    }
  }

