  // Transfer ilemleri iin doal dil ayrtrma
  static async parseTransfer(input: string, accounts?: any[]): Promise<{
    type: 'transfer';
    amount: number;
    description: string;
    from_account_id: string;
    to_account_id: string;
    summary?: string;
  }> {
    try {
      console.log(' Gemini API - Transfer ayrtrma balatlyor...');
      console.log(' Input:', input);
      
      const accountsText = accounts && accounts.length > 0
        ? `\nMevcut hesaplar:\n${accounts.map(acc => `- ${acc.name} (ID: ${acc.id}, Tr: ${acc.type})`).join('\n')}`
        : '';

      const prompt = `
        Bu doal dil ifadesini TRANSFER ilem verilerine evir:
        "${input}"
        
        Transfer lemleri rnekleri:
        - "Trkiye Finans'tan 8000 lira ektim"  transfer (banka  nakit)
        - "Banka hesabmdan czdanma para ektim"  transfer (banka  nakit)
        - "ATM'den para ektim"  transfer (banka  nakit)
        - "Kredi kart demesi yaptm"  transfer (banka  kredi kart)
        - "Hesaplar aras transfer"  transfer (hesap1  hesap2)
        
        Hesap Eletirme:
        - "Trkiye Finans"  Trkiye Finans hesab
        - "banka"  banka hesab
        - "nakit", "czdan", "para ektim"  nakit hesab
        - "kredi kart"  kredi kart hesab
        
        Kurallar:
        - Tutar saysal deer olarak kar (TL, lira, dolar, $, USD gibi birimleri kaldr)
        - lem tr her zaman "transfer" olmal
        - Gnderen hesab (from_account_id) ve alc hesab (to_account_id) belirle
        - Aklamay ksa ve net yap
        - Transfer zeti ekle
        ${accountsText}
        
        NEML: Response'da mutlaka ID'leri kullan, isimleri deil!
        
        Sadece JSON formatnda yantla:
        {
          "type": "transfer",
          "amount": 123.45,
          "description": "Transfer aklamas",
          "from_account_id": "gnderen_hesap_id",
          "to_account_id": "alc_hesap_id",
          "summary": "Transfer zeti"
        }
      `;

      console.log(' Gemini API Prompt:', prompt);
      
      const response = await this.callGeminiAPI(prompt);
      console.log(' Gemini API Response:', response);
      
      const cleanedResponse = this.cleanGeminiResponse(response);
      console.log(' Temizlenmi response:', cleanedResponse);
      
      const result = JSON.parse(cleanedResponse);
      console.log(' Ayrtrlm sonu:', result);
      
      // Transfer ilemleri iin hesap kontrol
      if (!result.from_account_id || !result.to_account_id) {
        console.error(' Transfer ilemi iin hesap ID\'leri eksik!');
        throw new Error('Transfer ilemi iin hesap bilgileri eksik');
      }
      
      return result;
    } catch (error) {
      console.error(' Gemini transfer ayrtrma hatas:', error);
      throw new Error('Transfer ayrtrlamad');
    }
  }

  // Doal dil ilem girii (genel - eski fonksiyon, geriye uyumluluk iin)
  static async parseNaturalLanguage(input: string, categories?: any[], accounts?: any[]): Promise<{
    type: 'income' | 'expense' | 'transfer';
    amount: number;
    description: string;
    category_id?: string;
    account_id?: string;
    from_account_id?: string;
    to_account_id?: string;
    vendor?: string;
    summary?: string;
  }> {
    try {
      console.log(' Gemini API - Genel doal dil ayrtrma balatlyor...');
      console.log(' Input:', input);
      
      // nce transfer olup olmadn kontrol et
      const textLower = input.toLowerCase();
      const isTransfer = textLower.includes('transfer') || 
                        textLower.includes('ektim') || 
                        textLower.includes('para ektim') ||
                        textLower.includes('atm') ||
                        textLower.includes('kredi kart demesi') ||
                        textLower.includes('gnderdim');
      
      if (isTransfer) {
        // Transfer ilemi iin zel fonksiyonu ar
        const transferResult = await this.parseTransfer(input, accounts);
        return {
          ...transferResult,
          category_id: undefined,
          account_id: undefined,
          vendor: undefined
        };
      } else {
        // Gelir/Gider ilemi iin zel fonksiyonu ar
        const incomeExpenseResult = await this.parseIncomeExpense(input, categories, accounts);
        return {
          ...incomeExpenseResult,
          from_account_id: undefined,
          to_account_id: undefined
        };
      }
    } catch (error) {
      console.error(' Gemini doal dil ayrtrma hatas:', error);
      throw new Error('Doal dil ayrtrlamad');
    }
  }

